<template>
  <!-- /login -->
  <!-- html and js autoinjects to App.vue (and therefore on public/index.html) -->
  <div class="container">
    <MainHeader />
    <div
      v-bind:class="{
        'container-transparent': !$store.getters.getTheme,
        'container-transparent-dark': $store.getters.getTheme,
      }"
      style="margin-top:100px"
    >
      <v-container style="width: 70%">
        <!-- if there are errors, show this div -->
        <div id="errordiv" v-if="errors.length">
          <ul class="collection with-header" style="border:1px solid red;">
            <li class="collection-header" style="background: rgba(0,0,0,0);">
              <v-alert type="error">
                Korjaa seuraavat virheet:
              </v-alert>
            </li>
            <li
              class="collection-item"
              id="error"
              v-for="(error, index) in errors"
              v-bind:key="index"
            >
              <p class="flow-text">{{ index + 1 }}. {{ error }}</p>
            </li>
          </ul>
        </div>
        <h1>Login</h1>
        <form>
          <v-row>
            <v-col md="8" offset-md="2" class="input-fields">
              <v-text-field
                :dark="$store.getters.getTheme"
                id="name"
                label="Käyttäjänimi"
                v-model="name"
                maxlength="40"
                :loading="loading"
                :counter="40"
              />
            </v-col>
          </v-row>
          <v-row>
            <v-col md="8" offset-md="2" class="input-fields">
              <v-text-field
                :dark="$store.getters.getTheme"
                label="Salasana"
                id="password"
                v-model="password"
                type="password"
                maxlength="40"
                :loading="loading"
                :counter="40"
              />
            </v-col>
          </v-row>
          <v-row v-if="loading">
            <ProgressBarQuery style="margin-bottom:20px" />
          </v-row>
          <v-row v-else>
            <v-col>
              <v-btn
                id="sbtn"
                large
                tile
                color="blue darken-4"
                class="white--text"
                @click="handleSubmit"
              >
                <i class="material-icons left">login</i>Kirjaudu
              </v-btn>
            </v-col>
          </v-row>
        </form>
      </v-container>
    </div>
  </div>
</template>
<script>
"use strict";
import M from "materialize-css";
import UserService from "../UserService";
import MainHeader from "../components/layout/MainHeader";
import ProgressBarQuery from "../components/layout/ProgressBarQuery";

export default {
  data() {
    return {
      errors: [],
      name: null,
      password: null,
      loading: false,
    };
  },
  components: {
    MainHeader,
    ProgressBarQuery,
  },
  mounted() {
    var input = document.getElementById("password");
    input.addEventListener("keyup", function(event) {
      if (event.keyCode === 13) {
        event.preventDefault();
        document.getElementById("sbtn").click();
      }
    });
    // Focus on top of the page when changing pages
    location.href = "#";
    location.href = "#app";
  },
  methods: {
    // Add error to error array and direct user to it
    showError: function(error) {
      this.errors.push(error);
      M.toast({ html: error });
      location.href = "#";
      location.href = "#app";
    },
    // Submit login credentials and confirm user login
    async handleSubmit(e) {
      e.preventDefault();
      this.errors = [];
      if (!this.name) {
        this.showError("Syötä Käyttäjänimi!");
      }
      if (!this.password) {
        this.showError("Syötä salasana!");
      }
      // If no errors in array, proceed to login
      if (!this.errors.length) {
        this.loading = true;
        // user object
        const user = {
          name: this.name,
          email: this.name,
          password: this.password,
        };
        try {
          // Login user  (check 'client\src\UserService.js' and 'server\routes\api\users.js' to see how this works)
          let res = await UserService.loginUser(user);
          if (res.success) {
            this.$store.state.is_admin = res.is_admin;
            this.$store.state.logged_in = true;
            if (this.$route.params.nextUrl != null) {
              this.$router.push(this.$route.params.nextUrl);
            }
            // if route.params.nextURL not set, redirect to dashboard
            else {
              this.$router.push({ path: "/dashboard" });
            }
            this.loading = false;
          } else {
            if (res.status === 401 || res.status === 404) {
              this.loading = false;
              this.showError("Käyttäjänimi tai salasana ei täsmää!");
              return;
            } else {
              this.loading = false;
              this.showError("Jokin meni vikaan, yritä uudestaan!");
              console.log(res.error, res.status);
            }
          }
        } catch (error) {
          console.log(error);
          this.showError("Näillä tiedoilla ei löytynyt käyttäjää!");
          this.$router.push({ path: "/login" });
        }
      }
    },
  },
};
</script>
